import Vue from 'vue'
import Router from 'vue-router'
import nav from './nav'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  linkActiveClass: 'a-active',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    ...nav,
    {
      path: '*',
      redirect: '/'
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.name === 'works' && to.params.id === undefined) {
    next({
      name: 'works',
      params: { id: 1 }
    })
  } else if (to.name === 'experience') {
    if (!to.params.year) {
      next({
        name: 'experience',
        params: { year: 2019, id: 1 }
      })
    } else {
      if (!to.params.id) {
        next({
          name: 'experience',
          params: { year: to.params.year, id: 1 }
        })
      } else {
        next()
      }
    }
  } else {
    next()
  }
})

export default router
