<template>
     <div class='nav'>
        <div class="mobile-nav">
                <div class="nav-btn" :class="{'nav-active':show}" @click='toggleNav'>
                    <div class="icon"></div>
                </div>
            <aside class='mask_nav' :class="{'active':show}">
                <div class="navigation-md">
                    <ul class="navigation-list" id="navigation_list">
                        <li
                        v-for="(item,index) in nav"
                        :key='item.name'
                        @click='toggleRouter(item.name)'
                        :class="{'a-active':item.name===$route.name}"
                        >
                            <a ref='icon' :class="iconArr[index]">{{item.title}}</a>
                        </li>
                    </ul>
                </div>
            </aside>
        </div>
        <aside class="navigation">
            <ul>
                <router-link v-for="(item,index) in nav" :key='item.name' tag="li" :to='{name:item.name}'>
                    <a ref='icon' :class="iconArr[index]"></a>
                    <i>{{item.title}}</i>
                </router-link>
            </ul>
        </aside>
    </div>
</template>

<script>
import nav from '@/nav'
const iconArr = ['icon-round-home-px', 'icon-round-person-px', 'icon-embed2', 'icon-icon-works', 'icon-experience', 'icon-mail']
export default {
    data() {
        return {
            show: false,
            nav,
            iconArr
        }
    },
    computed: {
        testDevice() {
            return /Mobile/g.test(navigator.userAgent)
        }
    },
    methods: {
        toggleNav() {
            this.show = !this.show
        },
        toggleRouter(name) {
            this.$router.push({ name: name })
            this.show = false
        }
    }
}
</script>

<style lang='less'>
.nav{
    position: absolute;
    top: 0px;
    height: 50px;
    width: 100%;
    z-index: 888;
}
.mobile-nav{
    position: relative;
    height: 100%;
    z-index: 50;
}
.nav-btn{
    transition: .5s background;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: absolute;
    right: 5%;
    top: 5px;
    z-index: 50;
    .icon{
        transition: .4s width;
        width: 26px;
        height: 4px;
        background: #fff;
        border-radius: 2px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -2px 0  0 -13px;
        &::before{
            content: '';
            border-radius: 2px;
            top: -8px;
            left: 0px;
            position: absolute;
            width: 26px;
            height: 4px;
            background: #fff;
            transition: .5s background, .4s transform;
        }
        &::after{
            border-radius: 2px;
            top: 8px;
            left: 0px;
            content: '';
            position: absolute;
            width: 26px;
            height: 4px;
            background: #fff;
            transition: .5s background, .4s transform;
        }
    }
}
.nav-active{
    background: #aaa9a9;
    .icon{
        width: 0;
        &::before{
            background: #fff;
            transform: translateY(8px) rotate(45deg) ;
            -webkit-transform:translateY(8px) rotate(45deg) ;
        }
        &::after{
            background: #fff;
            transform: translateY(-8px) rotate(-45deg)  ;
            -webkit-transform:translateY(-8px) rotate(-45deg)  ;
        }
    }
}
.mask_nav{
    opacity: 0;
    visibility: hidden;
    font-family: '微软雅黑';
    background: rgba(0,0,0,.5);
    height: 100vh;
    box-sizing: border-box;
    transition: .4s opacity,.4s visibility;
    &.active{
        opacity: 1;
        visibility:visible;
        .navigation-md{
            -webkit-transform: translateX(0px);
            transform: translateX(0px);
        }
    }
}
.navigation-md{
    transition: .4s transform;
    -webkit-transform: translateX(500px);
    transform: translateX(500px);
    width: 0;
    height: 0;
    float: right;
    border-bottom: #e4e2e3 solid;
    border-left: transparent 0 solid;
    border-bottom-width: 220vw;
    position: relative;
    width: 30%;
    border-left: transparent 150px solid;
}
.navigation-list{
    position: absolute;
    top: 80px;
    right: 0px;
    li{
        width: 140px;
        text-align: right;
        font-size: 18px;
        height: 70px;
        line-height: 70px;
        &.a-active a{
            color: #4c7ce4;
        }
        a{
            padding: 0px 25px 0 0;
            color: #686967;
            &::before{
                margin-right: 2px;
            }
        }
    }
}
.navigation{
    display: none;
    position: absolute;
    right: 50px;
    top: 50%;
    z-index: 999;
    margin-top: -81px;
    li{
        color: #fff;
        padding:15px 0;
        position: relative;
        i{
            border-radius: 2px;
            text-align: center;
            width: 60px;
            height: 25px;
            line-height: 25px;
            font-style: normal;
            position: absolute;
            top: 9px;
            left: -110px;
            background: #000;
            font-weight: 700;
            font-size: 14px;
            opacity: 0;
            transition: .5s transform;
            &::after{
                position: absolute;
                right: -24px;
                top: 0px;
                content: '';
                display:inline-block;
                width: 0;
                height: 0;
                border: 12.5px solid transparent;
                border-left-color: #000;
            }
        }
        a{
            width: 15px;
            height: 15px;
            background: #f1eef9;
            display: block;
            transition: .3s ease-out transform;
            border-radius: 50%;
            position: relative;
            &:hover{
                +i{
                    transform: translateX(15px);
                    -webkit-transform: translateX(25px);
                    opacity: 1;
                }
            }
            &::before{
                width: 15px;
                height: 15px;
                font-size: 8px;
                text-align: center;
                line-height: 16px;
                display: block;
                color:#fff;
                border-radius: 50%;
                transform: scale(0);
                -webkit-transform: scale(0);
            }
        }
    }
}
.navigation .a-active a{
    transform: scale(2.2);
    animation: 1.5s spread infinite ;
}
.navigation .a-active a::before{
    transform: scale(1);
    -webkit-transform: scale(1);
}

@keyframes spread{
    0% {
        box-shadow: 0 0 0 0 rgb(173, 197, 228);
        -webkit-box-shadow: 0 0 0 0 rgba(173, 200, 228, 1);
        background:rgba(3,71,108, 0.8)
    }
    100%{
        box-shadow: 0 0 0 5px rgba(173, 200, 228, 0);
        -webkit-box-shadow: 0 0 0 5px rgba(173, 200, 228, 0);
        background: rgb(3,71,108,1);
    }
}
@-webkit-keyframes spread{
    0% {
        box-shadow: 0 0 0 0 rgb(153, 189, 224,1);
        -webkit-box-shadow: 0 0 0 0 rgba(153, 189, 224, 1);
        background:rgba(112, 179, 241, 0.8)
    }
    100%{
        box-shadow: 0 0 0 5px rgba(153, 189, 224, 0);
        -webkit-box-shadow: 0 0 0 5px rgba(153, 189, 224, 0);
        background: rgb(112, 179, 241,1);
    }
}
@media all and (min-width:375px){
    .navigation-list{
        top: 100px;
        li{
            width: 150px;
            font-size: 23px;
            height: 80px;
            line-height: 80px;
        }
    }
}
@media all and (min-width:768px){
    .nav-btn{
        height: 80px;
        width: 80px;
        .icon{
            width: 26*2px;
            height: 8px;
            border-radius: 2px;
            margin: -2*2px 0  0 -13*2px;
            &::before{
                border-radius: 2px;
                top: -8*2px;
                width: 26*2px;
                height: 4*2px;
            }
            &::after{
                border-radius: 2px;
                top: 8*2px;
                width: 26px*2;
                height: 4px*2;
            }
        }
    }
    .nav-active{
        .icon{
            width: 0;
            &::before{
                transform: translateY(8*2px) rotate(45deg) ;
                -webkit-transform:translateY(8*2px) rotate(45deg) ;
            }
            &::after{
                transform: translateY(-8*2px) rotate(-45deg)  ;
                -webkit-transform:translateY(-8*2px) rotate(-45deg)  ;
            }
        }
    }
    .navigation-md{
        border-bottom-width: 220vw;
        width: 35%;
        border-left: transparent 270px solid;
    }
    .navigation-list{
        top: 80px*2;
        right: 0px;
        li{
            width: 150px*2;
            font-size: 18px*2;
            height: 70px*2;
            line-height: 70px*2;
            a{
                padding: 0px 25px*2 0 0;
            }
        }
    }
}
@media all and (min-width:960px){
    .navigation{
        display: block;
    }
    .nav{
        position: static;
    }
    .mobile-nav{
        display: none;
    }
    .nav-btn{
        margin-top: 30px;
    }
}
</style>
