export default [
    {
        path: '/home',
        name: 'home',
        title: '首页',
        meta: { index: 0 },
        component: () => import('./views/home')
    },
    {
        path: '/me',
        name: 'me',
        title: '关于我',
        meta: { index: 1 },
        component: () => import('./views/me')
    },
    {
        path: '/skill',
        name: 'skill',
        title: '技能掌握',
        meta: { index: 2 },
        component: () => import('./views/skill')
    },
    {
        path: '/works/:id?',
        name: 'works',
        title: '作品集',
        meta: { index: 3 },
        component: () => import('./views/works')
    },
    {
        path: '/experience/:year?/:id?',
        name: 'experience',
        title: '经历',
        meta: { index: 4 },
        component: () => import('./views/experience')
    }
]
